.plot-container {
    margin-top: 15px;
    display: flex;
    background-color: rgb(166,191,244);
    min-width: 40%;
    height: fit-content;
    border: 3px solid #1976d2;
    border-radius: 6px;
    box-shadow: 1px 1px 1px 1px #0000005e;
}

.plot-subcontainer {
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.stock-name {
    font-weight: bold;
    margin-bottom: 10px;
    margin-top: 5px;
    font-size: 1.25em;
}

.stock-info {
    text-align: center;
    margin-top: 5%;
    margin-left: 15px;
    margin-right: 15px;
    border-radius: 5px;
    background-color: #125495;
    color: white;
    height: fit-content;
    width: fit-content;
    padding: 5px;
    font-size: 1.1em;
}

.stock-info-line-item {
    margin-bottom: 5px;
}

.plot-label {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    width: fit-content;
    padding-inline: 5px;
    height: 25px;
    border-radius: 5px;
    background-color: #125495;
    font-family: sans-serif;
    box-shadow: 1px 1px 1px 1px #0000005e;
    margin-bottom: 7px;
    margin-top: 15px;
}

.range-button {
    background-color: #125495;
    color: white;
    padding: 5px;
    padding-inline: 7px;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px #0000005e;
    cursor: pointer;
}

.selected {
    font-weight: bold;
    background-color: #1770c8;
}

.range-button-container {
    margin-top: 7px;
    margin-bottom: 15px;
    display: flex;
    justify-content:space-around;
    width: 50%;
}

.message {
    margin-top: 15px;
    margin-bottom: 15px;
    text-align: center;
    width: 100%;
}

@media only screen and (max-width: 800px) {
    .plot-container {
        width: 98%;
        flex-direction: column;
        align-items: center;
    }

    .plot-subcontainer {
        margin-left: unset;

    }

    .range-button {
        margin-right: 5px;
    }

    .range-button-container {
        width: unset;
    }

    .stock-info {
        min-width: fit-content;
        width: 75%;
        margin-left: unset;
        margin-right: unset;
        margin-bottom: 15px;
    }
}